import { data } from "./story";
import "./App.css";
import { Collapse, Typography } from "antd";
const { Panel } = Collapse;
const { Title } = Typography;

function App() {
  return (
    <div className="App">
      <Title level={5}>website for bedtime story.have a good night!</Title>
      <Collapse defaultActiveKey={data[0].time}>
        {data.map((item) => (
          <Panel header={item.time} key={item.time}>
            <div dangerouslySetInnerHTML={{ __html: item.story }} />
          </Panel>
        ))}
      </Collapse>
    </div>
  );
}

export default App;
